.main-navigation ul {
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;
}

.main-navigation a {
  font-size: 14px;
  font-weight: 700;
  color: #EDEDED;
  text-transform: uppercase;
  text-decoration: none;
}

@media (max-width: 767px) {
  .main-navigation {
    margin: 0 -15px 15px;
  }
  .main-navigation a {
    color: #fff;
    text-align: center;
    display: block;
    padding: 10px 15px;
  }
  .main-navigation a:active {
    color: #3D38B8;
    background: #fff;
  }
  .main-navigation > ul ul {
    display: none;
  }
  .main-navigation > ul ul a {
    text-align: left;
  }
  .main-navigation > ul > li > .nav-child-wrap {
    position: relative;
  }
  .main-navigation > ul > li > .nav-child-wrap .nav-child-toggle {
    position: absolute;
    top: 0;
    right: 0;
    width: 45px;
    height: 100%;
    background: url(../img/i/shevron-down.svg) no-repeat 50%;
    background-size: 16px;
    transition: background-color ease 0.3s;
  }
  .main-navigation > ul > li > .nav-child-wrap .nav-child-toggle:before {
    content: '';
    position: absolute;
    top: 7px;
    right: 7px;
    left: 7px;
    bottom: 7px;
    border-radius: 5px;
    transition: background-color ease 0.3s;
  }
  .main-navigation > ul > li > .nav-child-wrap .nav-child-toggle:active:before {
    background-color: rgba(255, 255, 255, 0.5);
  }
  .main-navigation > ul > li > .nav-child-wrap > a {
    padding: 10px 45px;
  }
}

@media (min-width: 768px) {
  .main-navigation > ul {
    display: flex;
    justify-content: space-between;
  }
  .main-navigation > ul > li > a {
    position: relative;
    transition: opacity 0.3s ease;
  }
  .main-navigation > ul > li > a:hover,
  .main-navigation > ul > li > a:focus {
    opacity: 0.75;
  }
  .main-navigation > ul > li > a:before {
    display: none;
    content: '';
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
    bottom: -20px;
    width: 10px;
    height: 10px;
    border: 6px solid transparent;
    border-bottom-color: #fff;
  }
  .main-navigation > ul > li.active > a:before,
  .main-navigation > ul > li > a:hover:before,
  .main-navigation > ul > li > a:focus:before {
    display: block;
  }
  .main-navigation > ul ul {
    display: none;
    position: absolute;
    z-index: 9;
    top: 100%;
    left: 0;
    padding-top: 8px;
  }
  .main-navigation > ul ul:before {
    content: '';
    position: absolute;
    top: -4px;
    left: 15px;
    border: 6px solid transparent;
    border-bottom-color: #4D47F0;
  }
  .main-navigation > ul ul:before {
    content: '';
    position: absolute;
    top: -4px;
    left: 15px;
    border: 6px solid transparent;
    border-bottom-color: #4D47F0;
  }
  .main-navigation > ul ul a {
    display: block;
    padding: 10px 15px;
    transition: background 0.3s ease;
    background: #4D47F0;
  }
  .main-navigation > ul ul li.active > a,
  .main-navigation > ul ul a:hover,
  .main-navigation > ul ul a:focus {
    background: #221DA0;
  }
  .main-navigation > ul li:hover > ul {
    display: block;
  }
}

